.NewR{
  width: 80%;
  background-color: rgba(240, 240, 240, 0.8);
  border-radius: 1rem;
  border: 0.1rem solid rgba(255, 255, 255, 0.4);
  box-shadow: 0.2rem 0.2rem 1rem rgba(20, 20, 20, 0.8);
  margin: 2rem auto;
}

.RecipeForm{
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  margin: 1rem auto;
}

.LabelTitle {
  font-size: 1.3rem;
  font-weight: 500;
}

.CreateForm {
  display: flex;
  flex-direction: column;
}

.CreateForm label {
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
}
.CreateForm div {
  display: flex;
  flex-wrap: wrap;
}

.CreateForm input{
  padding: 0.5rem
}

.CreateForm input, .CreateForm textarea{
  background-color: transparent ;
  border-bottom: 1px solid black;
  border-top: 1px solid transparent;
  border-left: 1px solid transparent;
  border-right: 1px solid transparent;
  margin-bottom: 40px;
  outline: none;
  transition: 0.4s border;
}

.CreateForm input:hover, .CreateForm input:focus,
.CreateForm textarea:hover, .CreateForm textarea:focus {
  border: 1px dashed black;
}

.DietsAndSubmitButton{
  display: flex;
  flex-direction: column;
}

.DietsLabel{
  padding: 0.5rem 0;
}

.CreateSubmitButton {
  margin: 1rem auto;
  padding: 1rem 2rem;
}

.NotLoggedIn {
  height: 20rem;
  width: 70%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  margin: auto;
}

.LoginButton {
  padding: 0.5rem 1rem;
}